import styled from "styled-components";
import { sourceHanSansJpBold } from "./fonts";
import customMedia from "./customMedia";
import colors from "../constants/colors";
import TextProps from "../Types/TextProps";
import size from "../constants/size";

export interface MainTextSourceHanSansJpProps extends TextProps {
}

/**
 * Styled component.
 */
export const MainTextSourceHanSansJp = styled.div<MainTextSourceHanSansJpProps>`
  color: ${ props => props.color ? props.color :  colors.navy };
  ${ sourceHanSansJpBold }
  line-height: ${ size.lineHeightMainText };
  text-align: ${ props => props.textAlign };
  word-break: break-all;
  ${ customMedia.greaterThan<MainTextSourceHanSansJpProps>("tb")`
    font-size: ${ props => props.fontSize.pc }px;
  ` };
  ${ customMedia.lessThan<MainTextSourceHanSansJpProps>("tb")`
    font-size: ${ props => props.fontSize.tb }px;
  ` };
  ${ customMedia.lessThan<MainTextSourceHanSansJpProps>("sp")`
    font-size: ${ props => props.fontSize.sp }px;
  ` };
`;