import { useState,  useEffect } from "react";

/**
 * useSetTransTime
 * @param callBack 
 * @param delay 
 * @returns callBack's trigger.
 */
const useSetTransTime = (callBack: () => void, delay: number, initTrigger = true) => {

  const [trigger, setTrigger] = useState(initTrigger);

  useEffect(() => {
    if (trigger) {
      const timer = setTimeout(callBack, delay);
      return () => clearTimeout(timer);
    }
  }, [trigger]);

  return setTrigger;
};

export default useSetTransTime;
