import { useEffect } from "react";
import { CreateBgColor, TransitionKeyContainer, CatchTransition } from "../containers";

const useInitBodyColor = (color: string) => {
  const { setBgColor } = CreateBgColor.useContainer();
  const { transitionKey } = TransitionKeyContainer.useContainer();
  const { transitionClass } = CatchTransition.useContainer();
  useEffect(() => {
    if (transitionKey === "init") {
      setBgColor(color);
    }
  }, []);

  useEffect(() => {
    if (transitionClass === "page-transition-enter-done" && transitionKey === "slideAsVerticalNext") {
      setBgColor(color);
    }
  }, [transitionClass]);
}

export default useInitBodyColor;
