import React, { memo } from "react";
import { StartButtonUnit } from "../../components/organisms/StartButtonUnit/StartButtonUnit";
import { RoundImage } from "../../components/atoms/RoundImage/RoundImage";
import useSetHeader from "../../hooks/useSetHeader";
import size from "../../constants/size";
import Ruby from "../../components/atoms/Ruby/Ruby";
import { ContentDesc } from "../../components/atoms/ContentDesc/ContentDesc";
import { Mb } from "../../styles/Mb";
import SetPageNum from "../../Types/SetPageNum";
import time from "../../constants/time";

export interface StartProps extends SetPageNum {
}

/**
 * Functional component.
 * @param props 
 */
export const Start: React.FC<StartProps> = memo(({
  setPageNum
}) => {

  useSetHeader({
    color: "orange",
    prevHref: "/menus/menu8/main",
    prevTransitionKey: "slideAsVerticalPrev"
  });

  const hankaku2Zenkaku = (str) => {
    return str.replace(/[0-9]/g, function(s) {
      return String.fromCharCode(s.charCodeAt(0) + 0xFEE0);
    });
  };

  return (
    <>
      <StartButtonUnit
        onceClick={ () => {
          setPageNum(state => state + 1);
        } }
        title={ <>ライフジャケットを<Ruby ruby="ちゃくよう">着用</Ruby>せよ！</> }
        description={
          <>
            <Mb
              size={ {
                pc: 20,
                tb: 20,
                sp: 20
              } }
            >
              <ContentDesc>
                <Ruby ruby="せいげんじかん">制限時間</Ruby>{ hankaku2Zenkaku(String(time.lifeJacketLimit)) }<Ruby ruby="びょう">秒</Ruby>
              </ContentDesc>
            </Mb>
          </>
        }
      >
        <RoundImage
          image="koudou_lifejacket_start.png"
          ratio={ size.illustRatio }
        />
      </StartButtonUnit>
    </>
  );
});