import { useEffect } from "react";
import { ControlButtons } from "../containers";
import { useControlButtonsProps } from "../containers/useControlButtons";

const useSetHeader = (props?: useControlButtonsProps, trigger?: any) => {
  const { visibleControlButtons, setVisibleControlButtons } = ControlButtons.useContainer();

  useEffect(() => {

    const init: useControlButtonsProps = {
      color: void(0),
      notation: "",
      prevHref: void(0),
      nextHref: void(0),
      prevClick: void(0),
      nextClick: void(0),
      prevCallBack: void(0),
      nextCallBack: void(0),
      isExistTower: true,
      prevTransitionKey: "init",
      nextTransitionKey: "init",
      pageProgress: void(0)
    }

    // Set props.
    if (props) {
      setVisibleControlButtons({
        ...init,
        ...props
      });
    } else {
      setVisibleControlButtons({
        ...visibleControlButtons,
        ...init
      });
    }

  }, [trigger]);

}

export default useSetHeader;
