import React, { memo } from "react";
import styled from "styled-components";

export interface RoundImageProps {
  ratio: number;
  image?: string;
}

/**
 * Styled component.
 */
const Main = styled.div.attrs<RoundImageProps>(
  (props) => ({
    style: {
      backgroundImage: props.image ? `url(/${ props.image })` : "none"
    }
  })
)<RoundImageProps>`
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
  position: relative;
  background-color: #ddd;
  overflow: hidden;
  &:before {
    content: "";
    display: block;
    padding-top: ${ props => props.ratio }%;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const RoundImage: React.FC<RoundImageProps> = memo(({
  ratio,
  image,
  children
}) => {

  return (
    <>
      <Main image={ image } ratio={ ratio }>{ children }</Main>
    </>
  );
});
