import React, { memo } from "react";
import styled from "styled-components";
import size from "../../../constants/size";
import colors from "../../../constants/colors";
import { MainTextSourceHanSansJp } from "../../../styles/MainTextSourceHanSansJp";

export interface SelectButtonBarProps {
  isActive: boolean;
}

/**
 * Styled component.
 */
const Main = styled(MainTextSourceHanSansJp).attrs<SelectButtonBarProps>(
  (props) => ({
    style: {
      backgroundColor: props.isActive
        ? colors.selectButtonActiveColor.bar
        : colors.selectButtonNormalColor.bar
    }
  })
)<SelectButtonBarProps>`
  z-index: 1;
  margin-left: 10px;
  padding: 12px 10px 12px ${ size.selectButtonIconSize / 2 + 10 }px;
  margin-left: ${ size.selectButtonIconSize / 2 }px;
  content: "";
  top: 0px;
  right: 0px;
  z-index: 0;
  box-shadow: rgb(73 143 139 / 30%) 0px 6px 6px;
  border-radius: 10px;
  transition: background-color .3s ease;
  width: 100%;
`;

/**
 * Functional component.
 * @param props 
 */
export const SelectButtonBar: React.FC<SelectButtonBarProps> = memo(({
  isActive,
  children
}) => {
  return (
    <>
      <Main
        fontSize={ {
          pc: 18,
          tb: 18,
          sp: 14
        } }
        isActive={ isActive }
      >
        { children }
      </Main>
    </>
  );
});