import { useState } from "react";
import useInterval from "use-interval";

/**
 * useCountReady
 * @param fn Ignite after count ready.
 * @returns 
 */
const useCountReady = (limit: number, trigger?: boolean) => {

  const [countReady, setCountReady] = useState(3);
  const [countTimer, setCountTimer] = useState(limit);
  const [acceptTransition, setAcceptTransition] = useState(false);
  const [stopTimer, setStopTimer] = useState(false);

  useInterval(() => {
    if (countReady) {
      if (countReady < 4) {
        if (countReady > 1) {
          setCountReady(state => state - 1);
        } else {
          setCountReady(4);
        }
      } else {
        setCountReady(NaN);
      }
    }

    if (isNaN(countReady)) {
      if (countTimer) {
        if (!stopTimer) setCountTimer(state => state - 1);
        if (countTimer === 1) setCountReady(0);
      }
    }
    if (countTimer === 0) setAcceptTransition(true);
  }, trigger ? 1000 : null);

  return {
    countReady: countReady,
    countTimer: countTimer,
    acceptTransition: acceptTransition,
    setStopTimer: () => setStopTimer(true),
    initCount: () => {
      setCountReady(3);
      setCountTimer(limit);
    }
  };
}

export default useCountReady;
