import React, { memo, useEffect, useState, Dispatch, SetStateAction } from "react";
import styled from "styled-components";
import { CountDownAnim, CountDownAnimProps } from "../../../components/atoms/CountDownAnim/CountDownAnim";
import { ImgWithCountDown } from "../../../components/molecules/ImgWithCountDown/ImgWithCountDown";
import { ImgWithPenguinResult } from "../../../components/molecules/ImgWithPenguinResult/ImgWithPenguinResult";

export interface JacketImageProps extends CountDownAnimProps {
  isPushed: boolean;
  progress: number;
  setImageWearing: Dispatch<SetStateAction<string>>;
}

/**
 * Styled component.
 */
const Main = styled.div`
`;
 
/**
 * Functional component.
 * @param props 
 */
export const JacketImage: React.FC<JacketImageProps> = memo(({
  isPushed,
  count,
  progress,
  setImageWearing
}) => {

  const images = [
    "koudou_lifejacket_1.png",
    "koudou_lifejacket_2.png",
    "koudou_lifejacket_3.png",
    "koudou_lifejacket_4.png",
    "koudou_lifejacket_5.png",
    "koudou_lifejacket_success.png"
  ];

  const [image, setImage] = useState(images[0]);

  useEffect(() => {
    const currentImage = images[progress];
    setImage(currentImage);
    setImageWearing(currentImage);
  }, [progress]);

  return (
    <>
      {
        images.map((path, num) => (
          <link key={ num } rel="preload" href={"/" + path} as="image" />
        ))
      }
      { isPushed && <ImgWithPenguinResult image={ image } /> }
      {
        !isPushed && 
        <ImgWithCountDown
          image={ image }
        >
          <CountDownAnim count={ count } />
        </ImgWithCountDown>
      }
    </>
  );
});