import React, { useState, useEffect } from "react";
import { OpacityEffect } from "../components/atoms/OpacityEffect";
import SetPageNum from "../Types/SetPageNum";

type UseSwitchPageType
  = (setPageNum: SetPageNum["setPageNum"], pageNum: number) => React.ReactElement[];

const useSwitchPage = (contents: UseSwitchPageType) => {
  const [pageNum, setPageNum] = useState(0);
  const [pages, setPages] = useState([]);

  useEffect(() => {
    setPages(
      contents(setPageNum, pageNum).map((x, num) => {
        return (
          <div key={ num }>
            <OpacityEffect visible={ num === pageNum } isAbort={ num === 0 } isEnteredDelay={ true }>
              { x }
            </OpacityEffect>
          </div>
        );
      })
    )
  }, [pageNum]);

  return pages;
}

export default useSwitchPage;
