import React, { memo } from "react";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { RoundImage } from "../../components/atoms/RoundImage/RoundImage";
import { Mb } from "../../styles/Mb";
import { ResultBalloon } from "../../components/molecules/ResultBalloon/ResultBalloon";
import { QuestionResultText } from "../../components/atoms/QuestionResultText/QuestionResultText";
import size from "../../constants/size";
import Ruby from "../../components/atoms/Ruby/Ruby";
import useSetHeader from "../../hooks/useSetHeader";
import { StartButton } from "../../components/molecules/StartButton/StartButton";
import styled from "styled-components";
import customMedia from "../../styles/customMedia";
import SetPageNum from "../../Types/SetPageNum";
import useWindowTop from "../../hooks/useWindowTop";
import time from "../../constants/time";

export interface MainProps extends SetPageNum {
  timer: number;
  pageNum: number;
  imageWearing: string;
}

const StartButtonWrapperPc = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  ${ customMedia.lessThan("sp")`
    display: none;
  ` };
`;

const StartButtonWrapperSp = styled.div`
  margin-top: 14px;
  ${ customMedia.greaterThan("sp")`
    display: none;
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export const Result: React.FC<MainProps> = memo(({
  timer,
  setPageNum,
  imageWearing
}) => {

  useWindowTop(time.switchPageSystemTransitionDelay);
  useSetHeader({
    color: "orange",
    nextHref: "/menus/menu8/main",
    nextTransitionKey: "slideAsVerticalPrev",
    notation: "おわり"
  });

  const startButtonClickHandle = () => setPageNum(0);

  return (
    <>
      <Mb
        size={ {
          pc: 10,
          tb: 10,
          sp: 10
        } }
      >
        <QuestionTitle icon="quiz_icon.png">{ <><Ruby ruby="けっかはっぴょう">結果発表</Ruby>！</> }</QuestionTitle>
      </Mb>
      <Mb
        size={ {
          pc: 50,
          tb: 50,
          sp: 50
        } }
      >
        {
          !!timer && 
            <QuestionResultText
              top={ <><Ruby ruby="のこ">残</Ruby>り<Ruby ruby="じかん">時間</Ruby></> }
              bottom={ <>{ timer }<Ruby ruby="びょう">秒</Ruby></> }
            />
        }
        {
          !timer && 
            <QuestionResultText
              bottom={ <><Ruby ruby="じかん">時間</Ruby><Ruby ruby="ぎ">切</Ruby>れ</> }
            />
        }
      </Mb>
      {
        !!timer && 
          <ResultBalloon condition={ true } text={ <><Ruby ruby="ちゃくようせいこう">着用成功</Ruby>！</> }>
            <RoundImage image="koudou_lifejacket_success.png" ratio={ size.illustRatio } />
          </ResultBalloon>
      }
      {
        !timer && 
          <ResultBalloon condition={ false } text={ <><Ruby ruby="ちゃくようしっぱい">着用失敗</Ruby>…</> }>
            <RoundImage
              image={
                {
                  "koudou_lifejacket_1.png": "koudou_lifejacket_1timeup.png",
                  "koudou_lifejacket_2.png": "koudou_lifejacket_2timeup.png",
                  "koudou_lifejacket_3.png": "koudou_lifejacket_3timeup.png",
                  "koudou_lifejacket_4.png": "koudou_lifejacket_4timeup.png",
                  "koudou_lifejacket_5.png": "koudou_lifejacket_5timeup.png"
                }[imageWearing]
              }
              ratio={ size.illustRatio }
            >
              <StartButtonWrapperPc onClick={ startButtonClickHandle }>
                <StartButton>もう<Ruby ruby="いちど">一度</Ruby>！</StartButton>
              </StartButtonWrapperPc>
            </RoundImage>
            <StartButtonWrapperSp onClick={ startButtonClickHandle }>
              <StartButton>もう<Ruby ruby="いちど">一度</Ruby>！</StartButton>
            </StartButtonWrapperSp>
          </ResultBalloon>
      }
    </>
  );
});