import React, { memo, useEffect, useState, Dispatch, SetStateAction } from "react";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { Mb } from "../../styles/Mb";
import { JacketSelectButtonUnit } from "../../components/organisms/JacketSelectButtonUnit/JacketSelectButtonUnit";
import useSetHeader from "../../hooks/useSetHeader";
import { TopFixedTimer } from "../../components/molecules/TopFixedTimer/TopFixedTimer";
import useCountReady from "../../hooks/useCountReady";
import Ruby from "../../components/atoms/Ruby/Ruby";
import _ from "underscore";
import time from "../../constants/time";
import SetPageNum from "../../Types/SetPageNum";
import useSetTransTime from "../../hooks/useSetTransTime";
import { JacketImage, JacketImageProps } from "../../components/organisms/JacketImage/JacketImage";
import useWindowTop from "../../hooks/useWindowTop";

export interface MainProps extends SetPageNum {
  setTimer: Dispatch<SetStateAction<number>>;
  setImageWearing: JacketImageProps["setImageWearing"];
}

/**
 * Functional component.
 * @param props 
 */
export const Main: React.FC<MainProps> = memo(({
  setPageNum,
  setTimer,
  setImageWearing
}) => {

  useWindowTop(time.switchPageSystemTransitionDelay);
  useSetHeader();

  // Counter stuff.
  const { countReady, acceptTransition, setStopTimer, countTimer } = useCountReady(time.lifeJacketLimit, true);
  useEffect(() => setTimer(countTimer), [countTimer]);

  // Button stuff.
  const buttonsList = [
    {
      symbol: "1",
      children: <><Ruby ruby="りょううで">両腕</Ruby>を<Ruby ruby="とお">通</Ruby>して<Ruby ruby="き">着</Ruby>る</>
    },
    {
      symbol: "2",
      children: <><Ruby ruby="まえ">前</Ruby>のファスナーを<Ruby ruby="うえ">上</Ruby>まで<Ruby ruby="あ">上</Ruby>げる</>
    },
    {
      symbol: "3",
      children: <>お<Ruby ruby="なか">腹</Ruby>や<Ruby ruby="むね">胸</Ruby>のバックルをパチンとはめる</>
    },
    {
      symbol: "4",
      children: <>お<Ruby ruby="なか">腹</Ruby>や<Ruby ruby="むね">胸</Ruby>のベルトをきつめにしめる</>
    },
    {
      symbol: "5",
      children: <><Ruby ruby="また">股</Ruby>ベルトのバックルをはめて、ベルトをしめる</>
    }
  ];

  // Move to next page.
  const [progress, setProgress] = useState(0);
  useEffect(() => {
    if (progress === buttonsList.length) {
      setStopTimer();
      const timer = setTimeout(() => {
        setPageNum(state => state + 1);
      }, time.soundDistance);
      return () => clearTimeout(timer);
    }
  }, [progress]);

  useEffect(() => {
    if (acceptTransition) setPageNum(state => state + 1);
  }, [acceptTransition]);

  // Button pushed action.
  const [isPushed, setPushed] = useState(false);
  const madePushedReverse = useSetTransTime(() => {
    setPushed(false);
    madePushedReverse(false);
  }, time.soundDistance, false);

  return (
    <>
      <TopFixedTimer count={ countTimer } />
      <Mb size={ {
        pc: 30,
        tb: 30,
        sp: 30
      } }>
        <QuestionTitle icon="simulation_icon.png">
          <Ruby ruby="ただ">正</Ruby>しい<Ruby ruby="じゅん">順</Ruby>に<Ruby ruby="お">押</Ruby>してライフジャケットを<Ruby ruby="き">着</Ruby>せてみよう
        </QuestionTitle>
      </Mb>
      <Mb size={ {
        pc: 30,
        tb: 30,
        sp: 30
      } }>
        <JacketImage
          isPushed={ isPushed }
          count={ countReady }
          progress={ progress }
          setImageWearing={ setImageWearing }
        />
      </Mb>
      <JacketSelectButtonUnit
        pointerAccept={ isNaN(countReady) }
        buttonsList={ buttonsList }
        onClick={ (symbol) => {
          if (isNaN(countReady) && !isPushed) {

            const listStuff = buttonsList[progress];
            if (!listStuff) return false;
  
            const isMatch = symbol === listStuff.symbol;
            if (isMatch) {
              setProgress(state => state + 1);
            } else {
              setPushed(true);
              madePushedReverse(true);
            }
  
            return isMatch;
          }
        } }
      />
    </>
  );
});