import React, { memo } from "react";
import { ContentWrapper, ContentWrapperProps } from "../../organisms/ContentWrapper/ContentWrapper";
import useInitBodyColor from "../../../hooks/useInitBodyColor";
import colors from "../../../constants/colors";

export interface ContentProps extends ContentWrapperProps {
}
 
/**
 * Functional component.
 * @param props 
 */
export const Content: React.FC<ContentProps> = memo(({
  children,
  color
}) => {

  useInitBodyColor(color);

  return (
    <>
			<ContentWrapper color={ color }>{ children }</ContentWrapper>
    </>
  );
});