import React, { memo, useEffect, useState } from "react";
import styled from "styled-components";
import { SelectButton } from "../../molecules/SelectButton/SelectButton";
import _ from "underscore";

export interface JacketSelectButtonUnitProps {
  onClick: (e) => boolean;
  buttonsList: {
    symbol: string;
    children: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
  }[];
  pointerAccept: boolean;
}

/**
 * Styled component.
 */
const Main = styled.div`
  & > div {
    margin-bottom: 18px;
    &:last-child {
      margin-bottom: 0;
    }
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const JacketSelectButtonUnit: React.FC<JacketSelectButtonUnitProps> = memo(({
  pointerAccept,
  onClick,
  buttonsList
}) => {
  const [correctList, setCorrectList] = useState(buttonsList.map(x => false));
  const [buttons, setButtons] = useState<JacketSelectButtonUnitProps["buttonsList"]>();
  useEffect(() => setButtons(_.shuffle(buttonsList)), []);

  return (
    <>
      <Main>
        { buttons && buttons.map((x, num) => {
          return (
            <div key={ num }>
              <SelectButton
                isCorrect={ correctList[num] }
                isActiveDisplaySymbol={ correctList[num] }
                disablePointerEvents={ !pointerAccept }
                symbol={ x.symbol }
                onClick={ () => {
                  const isCorrect = onClick(x.symbol);

                  if (isCorrect) {
                    setCorrectList(state => state.map((_, stateIdx) => {
                      if (stateIdx === num) return true;
                      return _;
                    }));
                  }

                  return isCorrect;
                } }
              >
                { x.children }
              </SelectButton>
            </div>
          );
        }) }
      </Main>
    </>
  );
});