import Head from "next/head";
import React, { useState } from "react";
import { Start } from "../../../../contents/jacket/Start";
import { Main } from "../../../../contents/jacket/Main";
import { Result } from "../../../../contents/jacket/Result";
import useSwitchPage from "../../../../hooks/useSwitchPage";
import time from "../../../../constants/time";
import useSetHeader from "../../../../hooks/useSetHeader";
import { Content } from "../../../../components/templates/Content/Content";
import colors from "../../../../constants/colors";

export default function Page({
}) {
  useSetHeader();

  const [timer, setTimer] = useState(time.lifeJacketLimit);
  const [imageWearing, setImageWearing] = useState("");

  const content = useSwitchPage((setPageNum, pageNum) => {
    return [
      <Start setPageNum={ setPageNum } />,
      <Main setPageNum={ setPageNum } setTimer={ setTimer } setImageWearing={ setImageWearing } />,
      <Result setPageNum={ setPageNum } timer={ timer } pageNum={ pageNum } imageWearing={ imageWearing } />
    ];
  });

  return (
    <>
      <Head>
        <title>ライフジャケットを着用せよ！</title>
      </Head>
      <Content color={ colors.blue }>{ content }</Content>
    </>
  );
}
