import { useState, useEffect } from "react";
import useInterval from "use-interval";

export default () => {

  const fps = 15;

  // Set wave animation.
  const [loopAnimatePos, setLoopAnimatePos] = useState(0);
  useInterval(() => {
    setLoopAnimatePos(state => state > 1994 ? 0 : state + 6);
  }, 1000 / fps);

  // Set penguin animation.
  const [direction, setDirection] = useState(true);
  const [num, setNum] = useState(0);
  const speed = .04;
  useInterval(() => {
    setNum(state => {
      if (direction && num <= 1) {
        return state += speed;
      } else {
        setDirection(Math.sign(state) === -1);
        return state -= speed;
      }
    });
  }, 1000 / fps);

  const easeInOutSine = (x: number): number =>  -(Math.cos(Math.PI * x) - 1) / 2;
  const [penguinTransitionY, setPenguinTransitionY] = useState(0);
  useEffect(() => setPenguinTransitionY(easeInOutSine(num) * 10), [num, direction])

  return {
    loopAnimatePos, setLoopAnimatePos,
    penguinTransitionY, setPenguinTransitionY
  };
}
