import styled from "styled-components";
import { heiseiMaruGothicStdW8 } from "./fonts";
import customMedia from "./customMedia";
import colors from "../constants/colors";
import size from "../constants/size";
import TextProps from "../Types/TextProps";

export interface MainTextHeiseiMaruGothicProps extends TextProps {
}

/**
 * Styled component.
 */
export const MainTextHeiseiMaruGothic = styled.div<MainTextHeiseiMaruGothicProps>`
  color: ${ props => props.color ? props.color :  colors.navy };
  ${ heiseiMaruGothicStdW8 }
  line-height: ${ size.lineHeightMainText };
  text-align: ${ props => props.textAlign };
  word-break: break-all;
  ${ customMedia.greaterThan<MainTextHeiseiMaruGothicProps>("tb")`
    font-size: ${ props => props.fontSize.pc }px;
  ` };
  ${ customMedia.lessThan<MainTextHeiseiMaruGothicProps>("tb")`
    font-size: ${ props => props.fontSize.tb }px;
  ` };
  ${ customMedia.lessThan<MainTextHeiseiMaruGothicProps>("sp")`
    font-size: ${ props => props.fontSize.sp }px;
  ` };
`;
