import React, { memo, useEffect, useState } from "react";
import styled, { css } from "styled-components";

export interface CountDownAnimProps {
  count: number;
}

/**
 * Styled component.
 */
const Main = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  &:before {
    content: "";
    display: block;
    padding-top: 33.455%;
  }
`;

const animationDuration = 400;
interface ImageProps {
  imageUrl: string;
  imageSize: number;
  isVisible: boolean;
  animation?: string;
}
const Image = styled.div.attrs<ImageProps>(
  (props) => ({
    style: {
      maxWidth: props.imageSize + "px",
      backgroundImage: `url(/${ props.imageUrl })`,
      transition: props.isVisible ? `${ animationDuration }ms ease` : "none",
    }
  })
)<ImageProps>`
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition-property: opacity;
  opacity: 0;
  &.visible {
    opacity: 1;
    ${ props => props.animation }
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const CountDownAnim: React.FC<CountDownAnimProps> = memo(({
  count
}) => {

  const images = {
    0: {
      path: "danger_text_timeup.png",
      size: 376,
      animation: css`
        animation: TimeUpAnimation ${ animationDuration }ms;
        @keyframes TimeUpAnimation {
          0% {
            transform: scale(1);
          }

          40% {
            transform: scale(5);
          }

          50% {
            transform: scale(.95);
          }

          60% {
            transform: scale(4);
          }

          70% {
            transform: scale(.95);
          }

          80% {
            transform: scale(2);
          }

          100% {
            transform: scale(1);
          }
        }
      `
    },
    1: {
      path: "game_text_1.png",
      size: 176,
      animation: css`
      `
    },
    2: {
      path: "game_text_2.png",
      size: 176,
      animation: css`
      `
    },
    3: {
      path: "game_text_3.png",
      size: 176,
      animation: css`
      `
    },
    4: {
      path: "game_text_start.png",
      size: 376,
      animation: css`
      `
    }
  }

  const [isVisible, setIsVisible] = useState(false);
  useEffect(() => setIsVisible(true), []);

  return (
    <>
      <Main>
        { Object.keys(images).map((x, num) => 
          <Image
            key={ num }
            imageUrl={ images[x].path }
            imageSize={ images[x].size }
            className={ x === String(count) ? "visible" : null }
            animation={ images[x].animation }
            isVisible={ isVisible }
          />
        ) }
      </Main>
    </>
  );
});
