import React, { memo } from "react";
import styled from "styled-components";
import { sidePadding } from "../../../styles/wrapper";
import size from "../../../constants/size";

export interface HeaderOvrProps {
}

/**
 * Styled component.
 */
const Main = styled.a.attrs<HeaderOvrProps>(
  (props) => ({
    style: {
    }
  })
)<HeaderOvrProps>`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  ${ sidePadding }
  z-index: 1;
  &:before {
    content: "";
    background-color: #fff;
    padding-top: ${ size.headerOvrGap + size.controlUnitTopMargin }px;
    display: block;
    margin: 0 auto;
    max-width: ${ size.contentMaxWidth }px;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const HeaderOvr: React.FC<HeaderOvrProps> = memo(({
}) => {
  return (
    <>
      <Main />
    </>
  );
});