import React, { memo } from "react";
import styled, { css } from "styled-components";
import { RoundImage, RoundImageProps } from "../../atoms/RoundImage/RoundImage";
import size from "../../../constants/size";

export interface ImgWithPenguinResultProps {
  image?: RoundImageProps["image"];
  ratio?: RoundImageProps["ratio"];
}

/**
 * Styled component.
 */
const Image = css`background-image: url(/koudou_lifejacket_miss.png);`;
const Main = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: auto;
  height: 100%;
  text-align: center;
  background-size: contain;
  background-repeat: no-repeat;
  animation: ImgWithPenguinResult linear .7s;
  @keyframes ImgWithPenguinResult {
    0% {
      ${ Image }
      background-color: rgba(0, 0, 0, 0);
      background-position: 700px 0;
    }
    28% {
      ${ Image }
      background-color: rgba(0, 0, 0, .6);
      background-position: 0 0;
    }
    72% {
      ${ Image }
      background-color: rgba(0, 0, 0, .6);
      background-position: 0 0;
    }
    100% {
      ${ Image }
      background-color: rgba(0, 0, 0, 0);
      background-position: -700px 0;
    }
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const ImgWithPenguinResult: React.FC<ImgWithPenguinResultProps> = memo(({
  image,
  ratio
}) => {
  return (
    <>
      <RoundImage
        image={ image }
        ratio={ ratio ? ratio : size.illustRatio }
      >
        <Main />
      </RoundImage>
    </>
  );
});