import styled from "styled-components";
import DependsSize from "../Types/DependsSize";
import customMedia from "./customMedia";

export interface MbProps extends DependsSize {
}

/**
 * Styled component.
 */
export const Mb = styled.div<DependsSize>`
  margin-bottom: ${ props => props.size.pc ? props.size.pc : 0 }px;
  ${ customMedia.lessThan<DependsSize>("tb")`
    margin-bottom: ${ props => props.size.tb ? props.size.tb : 0 }px;
  ` };
  ${ customMedia.lessThan<DependsSize>("sp")`
    margin-bottom: ${ props => props.size.sp ? props.size.sp : 0 }px;
  ` };
`;
