import React, { memo } from "react";
import styled, { css } from "styled-components";
import { heiseiMaruGothicStdW8 } from "../../../styles/fonts";
import customMedia from "../../../styles/customMedia";
import colors from "../../../constants/colors";

export interface QuestionTitleProps {
  icon?: string;
}

/**
 * Styled component.
 */

const common = css`
  position: relative;
  ${ heiseiMaruGothicStdW8 }
  color: ${ colors.navy };
  line-height: 1.5;
  font-size: 36px;
  ${ customMedia.lessThan("sp")`
    font-size: 28px;
    padding-left: 0;
    text-align: center;
  ` };
`;

const circleSize = 60;
const Main = styled.div<QuestionTitleProps>`
  ${ common }
  padding-left: ${ circleSize + 10 }px;
  &:before {
    content: "";
    color: #fff;
    background-image: url("/${ props => props.icon }");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: ${ circleSize }px;
    height: ${ circleSize }px;
    position: absolute;
    text-align: center;
    line-height: ${ circleSize }px;
    top: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    ${ customMedia.lessThan("sp")`
      display: none;
    ` };
  }
`;

const NoICon = styled.div`${ common }`;

/**
 * Functional component.
 * @param props 
 */
export const QuestionTitle: React.FC<QuestionTitleProps> = memo(({
  children,
  icon
}) => {
  return (
    <>
      {
        icon && <Main icon={ icon }>{ children }</Main>
      }
      {
        !icon && <NoICon>{ children }</NoICon>
      }
    </>
  );
});