import React, { memo } from "react";
import { MainTextHeiseiMaruGothic, MainTextHeiseiMaruGothicProps } from "../../../styles/MainTextHeiseiMaruGothic";
import TextAlignProps from "../../../Types/TextAlignProps";

export interface ContentTitleProps {
  textAlign?: TextAlignProps["textAlign"];
  color?: MainTextHeiseiMaruGothicProps["color"];
}

/**
 * Functional component.
 * @param props 
 */
export const ContentTitle: React.FC<ContentTitleProps> = memo(({
  children,
  textAlign,
  color
}) => {
  return (
    <>
      <MainTextHeiseiMaruGothic
        fontSize={ {
          pc: 36,
          tb: 36,
          sp: 28
        } }
        color={ color ? color : "#2B8875" }
        textAlign={ textAlign ? textAlign : "center" }
      >
        { children }
      </MainTextHeiseiMaruGothic>
    </>
  );
});
