
import React, { memo } from "react";
import styled from "styled-components";
import { sidePadding, mainWrapper } from "../../../styles/wrapper";
import colors from "../../../constants/colors";
import size from "../../../constants/size";
import { HeaderOvr } from "../../atoms/HeaderOvr/HeaderOvr";

export interface ContentWrapperProps {
  color: string;
}

/**
 * Styled component.
 */
const Wrapper = styled.div<ContentWrapperProps>`
  width: 100%;
  background-color: ${ props => props.color };
  &:before {
    display: ${ props => props.color === colors.orange ? "none" : "block" };
    content: "";
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-image: url("/menu_bg_fish.png"), url("/menu_bg_sea_under.png");
    background-size: contain;
    background-position: top center, bottom center;
    background-repeat: no-repeat;
  }
`;

const Main = styled.div`
  ${ sidePadding }
  padding-top: ${ size.headerOvrGap + size.controlUnitTopMargin + 20 }px;
  width: 100%;
  max-width: ${ size.contentMaxWidth }px;
  background-color: #fff;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;
  padding-bottom: 100px;
  z-index: 1;
`;

const ChildWrapper = styled.div`
  ${ mainWrapper }
`;

/**
 * Functional component.
 * @param props 
 */
export const ContentWrapper: React.FC<ContentWrapperProps> = memo(({
  children,
  color
}) => {
  return (
    <>
      <Wrapper color={ color }>
        <Main>
          <HeaderOvr />
          <ChildWrapper className="transitionFade">
            { children }
          </ChildWrapper>
        </Main>
      </Wrapper>
    </>
  );
});
