import React, { memo } from "react";
import styled from "styled-components";
import { heiseiMaruGothicStdW8 } from "../../../styles/fonts";

/**
 * Styled component.
 */
const Main = styled.div`
  text-align: center;
  color: #F53636;
  font-weight: bold;
  ${ heiseiMaruGothicStdW8 }
  font-size: 24px;
`;

/**
 * Functional component.
 * @param props 
 */
export const ContentDesc: React.FC = memo(({
  children
}) => {
  return (
    <>
      <Main>{ children }</Main>
    </>
  );
});
